.dom-modal {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 30;
  background-color: #fff;
  transition-duration: 0.3s;
  opacity: 0;
  transition-timing-function: ease-in-out;
  left: 0;
  transform: translateX(100%);
  opacity: 1;
}
.dom-modal.active {
  opacity: 1;
  transform: none;
}
.dom-modal .close-modal-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 24px;
  z-index: 30;
}
.dom-modal .hamburger {
  padding: 0;
}
.dom-modal .hamburger-inner,
.dom-modal .hamburger-inner:after,
.dom-modal .hamburger-inner:before {
  width: 24px;
}
.dom-modal .hamburger-inner,
.dom-modal .hamburger-inner:after,
.dom-modal .hamburger-inner:before {
  height: 2px;
}
.dom-modal .dom-custom-scrollbar {
  height: 100%;
}
.dom-modal .os-content {
  display: flex;
  flex-direction: column;
}
.dom-modal .os-content > * {
  margin-top: auto;
  margin-bottom: auto;
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
}
