.accordion-head {
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
}
.accordion-head:hover {
  color: #c51b31;
}
.accordion-head:hover .accordion-ico .ico:after,
.accordion-head:hover .accordion-ico .ico:before {
  background-color: #c51b31;
}
.accordion-ico {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
}
.accordion-ico .ico {
  position: relative;
  width: 18px;
  height: 18px;
}
.accordion-ico .ico:after,
.accordion-ico .ico:before {
  content: "";
  position: absolute;
  background-color: #121212;
  transition: 0.3s;
  display: block;
  opacity: 0.8;
}
.accordion-ico .ico:before {
  height: 2px;
  width: 100%;
  top: calc(50% - 1px);
  left: 0;
}
.accordion-ico .ico:after {
  width: 2px;
  height: 100%;
  top: 0;
  left: calc(50% - 1px);
}
.accordion-head.active .accordion-ico .ico:after {
  height: 0;
  opacity: 0;
  transform-origin: center center;
  top: 50%;
}
