.site-wrap {
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}
.site-wrap > * {
  width: 100%;
  flex-grow: 0;
  flex-shrink: 0;
}
.site-wrap .footer {
  margin-top: auto;
}
.ico {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ico > * {
  width: 22px !important;
  height: auto !important;
}
.ico.ico-life > * {
  width: auto !important;
  height: 26px !important;
}
.ico-left {
  position: relative;
  padding-left: 34px;
}
.ico-left .ico {
  position: absolute;
  left: 0;
  top: calc(50% - 24px / 2);
}
.svg-fill,
.svg-stroke {
  transition: 0.3s;
}
.opacity-load {
  opacity: 0;
  transition: 0.5s;
}
.left-border:before {
  background-color: #f6a623;
  margin-right: 0.5em;
  display: inline-block;
  content: "";
  height: 1em;
  width: 0.25em;
  font-family: inherit;
  transform: skew(-15deg);
  margin-bottom: -0.25em;
}
.shadow-intern {
  position: relative;
