.offcanvas-menu {
  padding: 0;
  overflow: hidden;
}
.offcanvas-menu.active .menu-item {
  transform: skew(-15deg);
  opacity: 1;
}
.offcanvas-menu.active .menu-item:first-child {
  transition: transform 0.5s ease 0.1s, opacity 0.5s ease 0.1s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(2) {
  transition: transform 0.5s ease 0.2s, opacity 0.5s ease 0.2s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(3) {
  transition: transform 0.5s ease 0.3s, opacity 0.5s ease 0.3s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(4) {
  transition: transform 0.5s ease 0.4s, opacity 0.5s ease 0.4s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(5) {
  transition: transform 0.5s ease 0.5s, opacity 0.5s ease 0.5s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(6) {
  transition: transform 0.5s ease 0.6s, opacity 0.5s ease 0.6s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(7) {
  transition: transform 0.5s ease 0.7s, opacity 0.5s ease 0.7s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(8) {
  transition: transform 0.5s ease 0.8s, opacity 0.5s ease 0.8s, background-color 0.3s ease;
}
.offcanvas-menu.active .menu-item:nth-child(9) {
  transition: transform 0.5s ease 0.9s, opacity 0.5s ease 0.9s, background-color 0.3s ease;
}
