/*==============================================================
*												SECTION - CONTACTS
=============================================================== */
.section-contacts .contacts-wrap {
  display: flex;
  justify-content: space-between;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
}
@media (max-width: 991.9px) {
  .section-contacts .contacts-wrap {
    flex-wrap: wrap;
  }
}
.section-contacts .contact-col {
  max-width: 100%;
  padding: 20px 10px;
  padding-top: 0;
}
@media (max-width: 991.9px) {
  .section-contacts .contact-col {
    width: 50%;
  }
}
.section-contacts .contact-row {
  display: flex;
  margin-left: -5px;
  margin-right: -5px;
}
@media (max-width: 767.9px) {
  .section-contacts .contact-row {
    flex-wrap: wrap;
  }
}
.section-contacts .contact-item {
  padding: 5px;
  padding-top: 0px;
  padding-bottom: 10px;
}
.section-contacts .contact-ico {
  width: 60px;
  height: 60px;
  border: 1px solid rgba(18, 18, 18, 0.1);
}
@media (max-width: 767.9px) {
  .section-contacts .contact-ico {
    width: 50px;
    height: 50px;
  }
}
.section-contacts .contact-ico > * {
  transition: 0.3s;
}
.section-contacts .contact-ico:hover {
  background-color: rgba(18, 18, 18, 0.1);
}
.section-contacts .contact-ico:hover > * {
  transform: scale(1.2);
}
.section-contacts a:hover {
  color: #f6a623;
}
.section-contacts .contact-title {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 30px;
}
@media (max-width: 991.9px) {
  .section-contacts .contact-title {
    font-size: 18px;
    margin-bottom: 15px;
  }
}
/*==============================================================
*												SECTION - EDITOR
=============================================================== */
.section-editor .section-title {
  margin-bottom: 40px;
  font-weight: bold;
  position: relative;
  padding-bottom: 40px;
}
.section-editor .section-title:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 140px;
  height: 4px;
  border-radius: 2px;
  background-color: #f6a623;
}
.editor-content {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: -20px;
}
@media (max-width: 575.9px) {
  .editor-content.left,
  .editor-content.right,
  .editor-content.center {
    width: 100%;
  }
}
.editor-content.center {
  width: calc(100% / 12 * 8);
}
.editor-content.left,
.editor-content.right {
  width: calc(100% / 12 * 10);
}
.editor-content.left {
  margin-right: auto;
}
.editor-content.right {
  margin-left: auto;
}
.editor-content.center {
  margin-left: auto;
  margin-right: auto;
}
.editor-content p,
.editor-content ul,
.editor-content ol {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}
.editor-content img {
  max-width: 100%;
  height: auto!important;
}
.editor-content .alignleft {
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
}
.editor-content .alignright {
  float: right;
  margin-left: 20px;
  margin-bottom: 20px;
}
.editor-content .aligncenter {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.editor-content a {
  color: #0069B7;
  text-decoration: underline;
}
.editor-content a:hover {
  color: #000;
}
.editor-content h1,
.editor-content h2,
.editor-content h3,
.editor-content h4,
.editor-content h5,
.editor-content h6,
.editor-content strong {
  font-weight: bold;
}
.editor-content h1,
.editor-content h2,
.editor-content h3 {
  margin-bottom: 0.6em;
}
.editor-content h4,
.editor-content h5,
.editor-content h6 {
  margin-bottom: 0.8em;
}
.editor-content strong {
  color: #f6a623;
}
.editor-content blockquote {
  margin: 10px;
  margin-left: 20px;
  padding: 0;
  padding-left: 20px;
  border-left: 3px solid #f6a623;
  padding-top: 7px;
  padding-bottom: 7px;
  background-color: #eeeded;
  color: #000;
}
.editor-content blockquote p {
  margin: 0;
}
.editor-content ul,
.editor-content ol {
  margin-left: 0;
  margin-top: 0;
  margin-right: 0;
  padding-left: 0;
  list-style-position: inside;
}
